<template>
  <div class="infoswarp" :style="{height: warpHeight + 'px'}">
    <!-- <HEADER></HEADER> -->
    <div class="infosborder">
      <div class='infoscon'>
        <!-- <p class='infostitle'>{{ title }}</p> -->
        <div class='infosform'>
          <mt-cell class="formcell">
            <img src="../../assets/phone_icon.png" alt="" width="20px" height="20px">
            <mt-field type="tel" v-model.number="phone.data" :state="phone.state" placeholder="请输入手机号">
              <mt-button size="small" type="primary" @click.native='handleSendCode' plain :disabled="!show" class="codebtn">
                <span v-show="show">获取验证码</span>
                <span v-show="!show" class="count">{{count}} s</span>
              </mt-button>
            </mt-field>
          </mt-cell>
          <mt-cell class="formcell">
            <img src="../../assets/password_icon.png" alt="" width="20px" height="20px">
            <mt-field type="number" v-model.number="code.data" :state="code.state" class="validate" placeholder="请输入验证码">
            </mt-field>
          </mt-cell>
          <a class="mint-cell mint-field numform">
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
              <div class="mint-cell-title">
                <span class="mint-cell-text">购买数量：</span>
              </div>
              <div class="mint-cell-value numcon">
                <div class="mint-field-other sign">
                  <img src="../../assets/minus.png" alt="" width="12px" @click="handleNum('minus')">
                </div>
                <input number="true" type="number" class="field-core" min="0" max="10" @input="handleInputNum" :value="sum.data">
                <div class="mint-field-other sign">
                  <img src="../../assets/plus.png" alt="" width="12px" @click="handleNum('plus')">
                </div>
              </div>
            </div>
            <div class="mint-cell-right"></div>
          </a>
          <div class="btncon">
            <mt-button size="large" type="primary" @click.native='handleSubmit' class='nextbtn'>下一步</mt-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style>
.infosborder {
  height: 100%;
  background: white;
}
.infoscon {
  height: 90%;
  width: 90%;
  padding: 5%;
}
.infostitle {
  font-size: 1.4em;
  /* padding-top: 20px; */
}
.infosform {
  width: 100%;
}
.numform input {
  text-align: center;
}
.numcon {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}
.sign {
  background: #f6f6f6;
  width: 20px;
  height: 20px;
}
.nextbtn {
  margin-top: 30px;
  border-radius: 30px !important;
  width: 80% !important;
}
.btncon {
  width: 100%;
  display: flex;
  flex: 1;
  justify-content: center;
}
.infosborder .codebtn {
  border-radius: 8px;
}
/* covered mint */
.infoswarp .formcell, .infoswarp .numform {
  border-bottom: 1px solid rgb(246, 246, 246);
}
.formcell .mint-cell-title {
  flex: 0;
}
.infosform .mint-cell-text, input{
  color: rgb(153, 153, 153)
}
.infosform .mint-field .mint-cell-title {
  width: auto;
}
.infosform .mint-field, .infosform .mint-cell-wrapper{
  background-image: none;
}
.infosform .mint-cell:last-child {
  background-image: none;
}
.infosform .mint-button--small {
  width: 86px;
  font-size: 0.75em;
}
.infosform .mint-field-core {
  /* border-bottom: 1px solid rgb(207, 207, 207); */
  /* line-height: 2; */
}
.infosform .mint-field-state {
  margin-left: 5px;
}
.field-core {
  -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border-radius: 0;
    border: 0;
    outline: 0;
    line-height: 1.6;
    font-size: inherit;
    width: 2rem;
    margin: 0 0.6rem;
    /* background: rgb(238, 238, 238); */
}
.numform .mint-cell-value {
  border-bottom: none;
}
/* covered end */
</style>

<script>
import {
  senSmsUsingGET,
  createOrderFromH5UsingPOST
} from '@/../client/javascript/vue-api-client.js';
import HEADER from '@/components/header.vue'
// YBB.hybrid.navigation.hide();

const TIME_COUNT = 60;

export default {
  name: 'infos',
  components: { HEADER },
  data () {
    return {
      warpHeight: null,
      bodyHeight: null,
      title: '填写购买信息',
      code: this.$route.params.data,
      show: true,
      count: '',
      timer: null,
      phone: {
        data: null,
        state: ''
      },
      code: {
        data: null,
        state: ''
      },
      sum: {
        data: 1,
        state: ''
      },
      seller: {
        data: 2
      }
    }
  },
  methods: {
    handleSendCode() {
      if (this.phone.state !== 'success') {
        this.$toast({ message: '请填写手机号', position: 'top' });
      } else {
        let that = this
        senSmsUsingGET({ mobile: this.phone.data }).then((res) => {
          that.$toast('短信已发送');
        });
        if (!this.timer) {
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.show = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1000);
        }
      }
    },
    handleSubmit() {
      if (
        this.phone.state === 'success' &&
        this.code.state === 'success' &&
        this.sum.state === 'success'
      ) {
        this.$indicator.open();
        //生成订单
        createOrderFromH5UsingPOST({
          mobile: this.phone.data,
          code: this.code.data,
          num: this.sum.data,
          seller: this.seller.data
        }).then(response => {
          if (response.data.code === 0) {
            this.$store.commit('createOrder', response.data.body);
            // window.localStorage.setItem('order',JSON.stringify(response.data.body));
            setTimeout(() => {
              this.$router.push('/payment');
              this.$indicator.close();
            }, 2000);
          }
        })
        this.$store.commit('buyCard', {
          sum: this.sum.data,
          phone: this.phone.data,
          code: this.code.data,
          payed: false
        });
      } else {
        this.$toast({ message: '请填写正确', position: 'top' });
      }
    },
    handleNum(type) {
      switch (type) {
        case 'minus':
          this.sum.data --;
          break;
        case 'plus':
          this.sum.data ++;
          break;
      }
    },
    handleInputNum (e) {
      this.sum.data = e.target.value.replace(/[^\d]/g,'');
    }
  },
  computed: {
    carded() {
      return this.$store.state.carded;
    }
  },
  watch: {
    'phone.data': function() {
      const reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
      if (!reg.test(this.phone.data)) {
        this.phone.state = 'error';
      } else {
        this.phone.state = 'success';
      }
    },
    'code.data': function() {
      const reg = 6 && /^\d{6}$/;
      if (!reg.test(this.code.data)) {
        this.code.state = 'error';
      } else {
        this.code.state = 'success';
      }
    },
    'sum.data': function(newQuestion, oldQuestion) {
      const max = 99;
      const min = 1;
      if(newQuestion < min){
        this.sum.data = min;
        this.sum.state = 'success';
      }
      else if(newQuestion > max){
        this.sum.data = max;
        this.sum.state = 'success';
      }
    },
    deep: true
  },
  mounted () {
    // this.warpHeight = this.bodyHeight = document.body.clientHeight
    if (this.sum.data === 1) {
      this.sum.state = 'success';
    }
    if (this.$store.state.carded.phone) {
      this.phone.data = this.$store.state.carded.phone;
    }
    YBB.hybrid.navigation.setTitle({title: '填写购买信息'})
  }
}
</script>



